.program-section {
  position: relative;
  background-color: $dark-color-black;

  @media screen and (min-width: $tablet) {
    &.wrap {
      padding: 60px 0 150px;
    }
  }
  .line {
    display: none;
    border-top: 1px solid $light-color-white;
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);

    @media screen and (min-width: $desktop) {
      display: block;
    }
  }
  @media screen and (min-width: $desktop) {
    &.wrap {
      padding: 120px 0 164px;
    }
  }

  .program-container {
    &::after {
      content: ' ';
      position: absolute;
      bottom: -46px;
      right: 0;
      display: block;
      width: 120px;
      height: 137px;
      background-image: url(./../images/mobile/program.png);
      background-size: cover;

      @media screen and (min-device-pixel-ratio: 2),
        screen and (-webkit-min-device-pixel-ratio: 2),
        screen and (-o-min-device-pixel-ratio: 2/1),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        & {
          background-image: url(./../images/mobile/program@2x.png);
        }
      }
      @media screen and (min-width: $tablet) {
        bottom: 0px;
        background-image: url(./../images/tablet/program.png);
        width: 384px;
        height: 280px;
        background-size: cover;

        @media screen and (min-device-pixel-ratio: 2),
          screen and (-webkit-min-device-pixel-ratio: 2),
          screen and (-o-min-device-pixel-ratio: 2/1),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          & {
            background-image: url(./../images/tablet/program@2x.png);
          }
        }
      }
      @media screen and (min-width: $desktop) {
        bottom: -85px;
        left: -25px;
        background-image: url(./../images/desktop/program.png);
        width: 553px;
        height: 450px;
        background-size: contain;
        background-repeat: no-repeat;

        @media screen and (min-device-pixel-ratio: 2),
          screen and (-webkit-min-device-pixel-ratio: 2),
          screen and (-o-min-device-pixel-ratio: 2/1),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          & {
            background-image: url(./../images/desktop/program@2x.png);
          }
        }
      }
    }
  }
}

.program-period {
  position: relative;
  top: -8px;

  @media screen and (min-width: $desktop) {
    .number {
      font-size: 30px;
    }
  }
}
.program-section .title {
  color: $light-color-white;
  margin-bottom: 43px;
  @media screen and (min-width: $tablet) {
    margin-bottom: 52px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 100px;
  }
}

.program-thumb {
  display: flex;

  @media screen and (max-width: 767px) {
    max-width: 276px;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-left: 68px;
  }
  @media screen and (max-width: 350px) {
    margin-left: auto;
  }

  @media screen and (min-width: 351px) and (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: $tablet) and (max-width: 1359px) {
    padding-left: 57px;
  }
}

.program-list {
  display: flex;
  flex-direction: column;
  padding-left: 0;

  @media screen and (min-width: $desktop) {
    flex-direction: row;
    padding: 60px 0 0;
    border-left: none;
    width: 100%;
    justify-content: space-between;
  }
}

.program-item {
  position: relative;

  font-weight: 700;
  line-height: 2;
  text-transform: uppercase;
  color: $light-color-white;
  padding-left: 26px;

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      padding-bottom: 40px;
    }
    @media screen and (max-width: 1359px) {
      border-left: 1px solid $light-color-white;
    }
    @media screen and (min-width: $tablet) and (max-width: 1359px) {
      padding-bottom: 30px;
    }
  }

  @media screen and (min-width: $tablet) {
    padding-left: 56px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 20px;
    line-height: 1.4;
    padding: 0;
    width: 240px;
  }
}

.program-item::before {
  content: '';
  position: absolute;
  // display: inline-flex;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: $bright-color-orange;
  @media screen and (max-width: 1359px) {
    top: -1px;
    left: -6.5px;
  }
  @media screen and (min-width: $desktop) {
    top: -69px;
    left: 0px;
    width: 17px;
    height: 16px;
  }
}

.program-skills-list {
  @media screen and (max-width: 767px) {
    margin-top: 5px;
  }
  @media screen and (min-width: $tablet) {
    display: flex;
  }
  @media screen and (min-width: $desktop) {
    flex-direction: column;
    margin-top: 25px;
    .program-skills-item + .program-skills-item {
      margin-top: 25px;
    }
  }
}

.program-skills-wrap {
  display: inline-block;
  @media screen and (max-width: 1359px) {
    line-height: 2.7;
  }
}

.program-skills-item {
  font-size: 12px;
  line-height: 1.667;
  text-transform: none;
  color: $bright-color-orange;

  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  @media screen and (min-width: $tablet) and (max-width: 1359px) {
    &:not(:last-child) {
      margin-right: 50px;
    }
    &:nth-child(1n) {
      width: 176px;
    }
    &:nth-child(2n) {
      width: 155px;
    }
    &:nth-child(3n) {
      width: 110px;
    }
  }

  @media screen and (min-width: $desktop) {
    font-size: 20px;
    line-height: 1.65;
  }
  &::before {
    content: '\2014\A0';
  }
}

.program-skills-item .list {
  font-weight: 500;
  color: $light-color-gray;
  opacity: 0.5;
  margin-top: 5px;
  .program-item:nth-child(3n) &,
  .program-item:nth-child(5n) & {
    margin-top: 10px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 18px;
    line-height: 1.556;
    margin-top: 10px;
  }
}
.program-item:nth-child(3n) .program-skills-item:nth-child(3n) .list {
  margin-top: 5px;
}
